import axios from 'axios';
import Product from '../components/productTemp';
import Heart from '../components/heart';
export default {
  name: 'Index',
  data () {
    return {
    	title:"",
		title2:"",
		subTitle:"",
		msg:"",
		list:[]
    }
  },
  components:{
  	"Temp":Product,
  	"Heart":Heart
  },
  beforeCreate(){
  	axios.post('/api/aaa').then((data)=>{
  			let a=data.data;
  			this.title=a.title;
  			this.title2=a.title2;
  			this.subTitle=a.subTitle;
  			this.msg=a.msg;
  	});
  	axios.post('/api/product').then((res)=>{
  		this.list=[...this.list,...res.data.list]
  		console.log(this.list)
  	})
  },
  mounted(){
  	var mySwiper = new Swiper('.swiper-container',{
	
		scrollbar: {
		    el: '.swiper-scrollbar',
		    draggable: true,
		    snapOnRelease: false,
		  },
		})
  	mySwiper.scrollbar.$dragEl.css('background','#ff6600');
  }
}




